@import '../resources/base.scss';

.home-content{
    max-width: 1440px;
    margin: 0 auto;
}

h2{
    text-align: center;
    margin: 120px auto;
    font-size: 56px;
}

.header-home {
    height: 50px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    select{
        padding: 5px 10px;
        border-color: var(--theme-orange);
        height: 30px;
        border-radius: 10px;
    }
    .selected-network{
        color: var(--theme-orange);
        padding: 10px 16px;
        font-size: 18px;
        font-weight: 500;
        background: #FFC59F;
        opacity: 0.7;
        box-shadow: 0px 0px 1.48148px #8393A3;
        border-radius: 20px
    }

    img{
        width: 50px;
        height: 50px;
        margin-right: 140px;
    }
}
main{
    display: flex;
    justify-content: center;
    min-width: 970px;
}
.vertical-space-divider{
    width: 24px
}